import React from 'react';
import s from './styles.module.css'
import {ReactComponent as Logo} from "../../../assets/svg/logo.svg";
import {ReactComponent as Logo2} from "../../../assets/svg/logo_seconf.svg";

const Footer = ({ref_i}) => {
    return (
        <div className={s.main} ref={ref_i}>
            <div className={s.main_left}>
                <Logo/>
                <p>На рынке покера с 2013 года</p>
            </div>
            <div className={s.main_right}>
                <p className={s.title}>спроектировано в</p>
                <Logo2/>
            </div>
        </div>
    );
};

export default Footer;
