import React from 'react';
import s from './styles.module.css'
import {ReactComponent as Logo} from "../../../assets/svg/logo_command.svg";

const Command = () => {
    return (
        <div className={s.command}>
            <div className={s.header}>
                <Logo/>
                <p>#команда</p>
            </div>

            <div className={s.content}>
                <h1 className={s.title}>Кто мы и что можем тебе предложить?</h1>
                <p className={s.sub_title}>Наша команда является одной из старейших на рынке. Мы успешно обучаем игроков
                    в покер с нуля более 7 лет и за это время накопили огромное количество опыта, которым готовы
                    поделиться в рамках бесплатного курса для начинающих.</p>
            </div>
        </div>
    );
};

export default Command;
