import React from 'react';
import s from './styles.module.css'
import Container from "../../component/container";
import Header from "./1-header";
import StartLearn from "./2-start_learn";
import List from "./3-list";
import Command from "./4-command";
import Analityk from "./5-ananlityc";
import Best from "./6-best";
import Progrm from "./7-programm";
import Blogs from "./8-blogs";
import Discord from "./9-discord";
import Author from "./10-author";
import Fqa from "./11-fqa";
import Footer from "./12-footer";
import Button from "../../component/button";
import AnchorLink from "react-anchor-link-smooth-scroll/lib/anchor-link";
import {useInView} from "react-intersection-observer";
import {useMediaQuery} from "@mui/material";

const Landing = () => {
    const matches = useMediaQuery('(max-width:700px)');
    const {ref, inView, entry} = useInView({
        threshold: .8,
    });
    return (
        <div className={s.landing}>
            <div className={s.btn_box} style={{
                bottom:
                    ((inView && !matches) && '98px') ||
                    ((!inView && !matches) && '32px') ||
                    ((inView && matches) ? '145px' : 0)
            }}>
                <AnchorLink
                    offset={30}
                    href={`#tg_block`}
                >
                    <Button className={s.btn}>Начать учиться</Button>
                </AnchorLink>
            </div>
            <Container>
                <Header/>
            </Container>
            <Container>
                <StartLearn/>
            </Container>
            <Container>
                <List/>
            </Container>
            <Container>
                <Command/>
            </Container>
            <Analityk/>
            <Container>
                <Best/>
            </Container>
            <Container>
                <Progrm/>
            </Container>
            <Container style={{position: 'relative'}}>
                <Blogs/>
            </Container>
            <Container>
                <Discord/>
            </Container>
            <Container>
                <Author/>
            </Container>
            <Container>
                <Fqa/>
            </Container>
            <Footer ref_i={ref}/>
        </div>
    );
};

export default Landing;
