import React, {useEffect, useLayoutEffect, useState} from 'react';
import s from './styles.module.css'
import classNames from "classnames";
import $ from "jquery";
import Button from "../../../component/button";
import blog1 from '../../../assets/img/blog_1.png'
import blog2b from '../../../assets/img/block2_b.png'
import blog2s from '../../../assets/img/block2_s.png'
import blog2bm from '../../../assets/img/imb_box_2_1_m.png'
import blog2sm from '../../../assets/img/img_box_2_2_m.png'
import {Box, Modal, styled, SwipeableDrawer, Tooltip, tooltipClasses, useMediaQuery} from "@mui/material";
import AnchorLink from "react-anchor-link-smooth-scroll/lib/anchor-link";
import {grey} from "@mui/material/colors";

const style = {
    // position: 'absolute',
    // bottom: '0px',
    width: '100%',
    background: '#2B2A37',
    boxShadow: 0,
    display: 'flex',
    flexDirection: 'column',
    borderRadius: '16px 16px 0px 0px',
    height: '120px',
    padding: '0 16px',
    alignItems: 'center',
    justifyContent: 'center',
    color: '#FFF',
    fontFamily: 'Inter,sans-serif',
    fontSize: '16px',
    fontStyle: ' normal',
    fontWeight: ' 400',
    lineHeight: '140%',
    paddingTop: '12px'
};

const Puller = styled(Box)(({theme}) => ({
    width: 30,
    height: 6,
    backgroundColor: grey[900],
    borderRadius: 3,
    position: 'absolute',
    top: 8,
    left: 'calc(50% - 15px)',
}));

const Blogs = () => {
    const matches = useMediaQuery('(max-width:700px)');
    const [open, setOpen] = useState(false)
    const HtmlTooltip = styled(({className, ...props}) => (
        <Tooltip {...props} classes={{popper: className}}/>
    ))(({theme}) => ({
        [`& .${tooltipClasses.tooltip}`]: {
            background: '#343434',
            padding: '16px',
            borderRadius: '10px',
            maxWidth: '255px',
            width: '100%'
        },
    }));
    useEffect(() => {
        $(function () {

            // Set up vars
            var _window = $(window),
                panels = $('.panel'),
                panelsY = [];
            $.each(panels, function (i, el) {
                panelsY.push(panels.eq(i).offset().top);
            });

            _window.bind('scroll', function () {
                updateWindow();
            });

            function updateWindow() {
                var y = _window.scrollTop();

                for (var i = 0, l = panels.length; i < l; i++) {

                    if ((i === l - 1) || (y >= panelsY[i] && y <= panelsY[i + 1])) {
                        break;
                    }
                }
                ;

                panels.not(':eq(' + i + ')').removeClass('panel-fixed');
                panels.eq(i).addClass('panel-fixed');
            };

        });
    })
    const toggleDrawer = (newOpen) => () => {
        setOpen(newOpen);
    };
    const container = window !== undefined ? () => window.document.body : undefined;
    const iOS =
        typeof navigator !== 'undefined' && /iPad|iPhone|iPod/.test(navigator.userAgent);
    return (
        <div className={'blogs_main'}>
            {/*<Modal*/}
            {/*    open={open}*/}
            {/*    onClose={() => setOpen(false)}*/}
            {/*>*/}
            {/*    <Box sx={style}>*/}
            {/*        Poker Hub — это веселая и бесплатная онлайн-платформа для любителей покера!*/}
            {/*    </Box>*/}
            {/*</Modal>*/}

            <SwipeableDrawer
                disableBackdropTransition={!iOS} disableDiscovery={iOS}
                sx={{

                    '& .MuiPaper-root': {
                        background: 'rgba(0, 0, 0, 0.5);',
                        borderRadius: '16px 16px 0px 0px',
                        // opacity:'0'
                    }
                }}
                container={container}
                anchor="bottom"
                open={open}
                onClose={toggleDrawer(false)}
                onOpen={toggleDrawer(true)}
                swipeAreaWidth={36}
                disableSwipeToOpen={true}
                ModalProps={{
                    keepMounted: true,
                }}
            >

                <div className={s.line}/>

                <Box sx={style} onClick={(e) => {
                    e.stopPropagation()
                    e.preventDefault()
                }}
                     onDrag={(e) => {
                         e.stopPropagation()
                         e.preventDefault()
                     }}

                >
                    Poker Hub — это веселая и бесплатная онлайн-платформа для любителей покера!
                </Box>
            </SwipeableDrawer>

            <div className="panel panel-one">
                <div className="panel-inner">
                    <div className={classNames(s.content, s.content_1)}>
                        <div className={s.content_left1}>
                            <div className={s.content_left1_top}>
                                <h1 className={s.title}>Доступ к курсу есть всегда</h1>
                                <p className={classNames(s.sub_title, s.sub_title1)}>Как только ты станешь участником
                                    курса,
                                    для тебя будет создан личный кабинет, в котором есть доступ к теории</p>
                            </div>
                            <div className={s.content_left1_bottom}>
                                <AnchorLink
                                    offset={30}
                                    href={`#tg_block`}
                                >
                                    <Button className={s.btn_1}>Учись в удобное время</Button>
                                </AnchorLink>
                            </div>
                        </div>

                        <img alt={'img_1'} src={blog1} className={s.img}/>
                    </div>
                </div>
            </div>

            <div className="panel panel-two">
                <div className="panel-inner">
                    <div className={classNames(s.content, s.content_2)}>
                        <div className={s.img_box2}>
                            <img alt={'img_1'} src={blog2b} className={s.img_box2_1}/>
                            <img alt={'img_1'} src={blog2s} className={s.img_box2_2}/>
                            <img alt={'img_1'} src={blog2bm} className={s.img_box2_11}/>
                            <img alt={'img_1'} src={blog2sm} className={s.img_box2_22}/>
                        </div>


                        <div className={classNames(s.content_left1, s.content_left2)}>
                            <div className={s.content_left1_top}>
                                <h1 className={s.title}>Улучшайте навыки на тренажере</h1>
                                <p className={classNames(s.sub_title, s.sub_title2)}>Наша совместная разработка <br/>
                                    с <HtmlTooltip placement="bottom-start"
                                                   title={<div className={s.tooltip}>Poker Hub — это веселая и
                                                       бесплатная
                                                       онлайн-платформа для любителей
                                                       покера!</div>}><span onClick={() => matches && setOpen(true)}>покер хаб</span></HtmlTooltip> позволит
                                    привыкнуть к игре и проверить, насколько усвоен
                                    материал урока.
                                    Тренажеры и тесты располагаются на страницах уроков с уже предустановленными
                                    настройками для отработки конкретной задачи, тебе остается только играть!</p>
                            </div>
                            <div className={classNames(s.content_left1_bottom, s.content_left1_bottom2)}>
                                <AnchorLink
                                    offset={30}
                                    href={`#tg_block`}
                                >
                                    <Button className={classNames(s.btn_1, s.btn_2)}>Как в реальной игре</Button>
                                </AnchorLink>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

            <div className="panel panel-three">
                <div className="panel-inner">
                    <div className={classNames(s.content, s.content_3)}>
                        <div className={s.content_left1}>
                            <div className={s.content_left1_top}>
                                <h1 className={s.title}>Огромное снг комьюнити</h1>
                                <p className={classNames(s.sub_title, s.sub_title3)}>В котором профи игроки и новички
                                    делятся опытом, рассказывают о своем пути в покере и просто классно общаются</p>
                            </div>
                            <div className={classNames(s.content_left1_bottom, s.content_left1_bottom3,)}>
                                <AnchorLink
                                    offset={30}
                                    href={`#tg_block`}
                                >
                                    <Button className={s.btn_1}>Учись у лучших</Button>
                                </AnchorLink>
                            </div>
                        </div>

                        <iframe className={s.iframe}
                                src="https://www.youtube.com/embed/jJ4WqxQzJf8?autoplay=1;loop=1;playlist=jJ4WqxQzJf8"
                                title="Большой обзор Pixel 7 Pro — iPhone 14 Pro Max, на колени!" frameBorder="0"
                            // allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share"
                                allowFullScreen></iframe>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default Blogs;
