import React from 'react';
import s from './styles.module.css'

const List = () => {
    return (
        <div className={s.list}>
            <div className={s.item1}>
                <div className={s.item_top}>
                    <div className={s.circle}/>
                    <p className={s.title}>Авторы курса</p>
                </div>
                <p className={s.sub_title}>Тренеры профи команд с опытом более 15 лет</p>
            </div>
            <div className={s.item2}>
                <div className={s.item_top}>
                    <div className={s.circle}/>
                    <p className={s.title}>Инвестируем</p>
                </div>
                <p className={s.sub_title}>В лучших игроков деньгами и помогаем во всем</p>
            </div>
            <div className={s.item3}>
                <div className={s.item_top}>
                    <div className={s.circle}/>
                    <p className={s.title}>Онлайн-тренажер</p>
                </div>
                <p className={s.sub_title}>Симулятор реальной игры собственной разработки</p>
            </div>
            <div className={s.item4}>
                <div className={s.item_top}>
                    <div className={s.circle}/>
                    <p className={s.title}>Комьюнити</p>
                </div>
                <p className={s.sub_title}>100+ игроков в команде, готовых делиться опытом</p>
            </div>
        </div>
    );
};

export default List;
