import React from 'react';
import s from './styles.module.css'
import {ReactComponent as DiscordIcon} from "../../../assets/svg/discord.svg";

const Discord = () => {
    return (
        <div className={s.main}>
            <div className={s.description_box}>
                <h2 className={s.title}>Обучение с ментором организовано в дискорд</h2>
                <p className={s.sub_title}>Очные занятия с тренером проходят по видеосвязи. У каждого обучающегося есть
                    чат для личных консультаций и групповой канал, в котором есть возможность выражать свои идеи,
                    обсуждать стратегии, участвовать в совместных тренировках и многое другое</p>
            </div>
            <div className={s.icon}>
                <DiscordIcon/>
            </div>
        </div>
    );
};

export default Discord;
