import React, {useState} from 'react';
import s from './styles.module.css'
import classNames from "classnames";
import {ReactComponent as Arrow} from "../../../assets/svg/arrow.svg";
import ModalSupport from "../../../component/modal/modal_support";

const Best = () => {
    const [open, setOpen] = useState(false)
    return (
        <div className={s.main}>
            <ModalSupport open={open} setOpen={setOpen} handleClose={() => setOpen(false)}/>
            <div className={s.main_left}>
                <h1 className={s.main_left_title}>лучшие ученики получат спонсорские контракты</h1>
                <p className={s.main_left_sub_title}>Мы меняем жизнь наших тиммейтов, которые добились успехов на курсе,
                    и предлагаем им стать частью нашей команды</p>
            </div>
            <div className={s.main_right}>
                <div className={s.item}>
                    <h2 className={classNames(s.number, s.number_1)}>1</h2>
                    <h1 className={s.item_title}>Бекинг</h1>
                    <p className={s.item_sub_title}>Твоя игра полностью финансируется командой</p>
                </div>
                <div className={s.item}>
                    <h2 className={s.number}>2</h2>
                    <h1 className={s.item_title}>Обучение после курса</h1>
                    <p className={s.item_sub_title}>Продолжим обучать бесплатно, а также разработаем для тебя
                        персональный план развития</p>
                </div>
                <div className={s.item}>
                    <h2 className={s.number}>3</h2>
                    <h1 className={s.item_title}>Поддержка игроков</h1>
                    <p className={s.item_sub_title}>Решаем ваши проблемы круглосуточно, где бы ты ни находился</p>
                </div>
                <div className={s.item}>
                    <h2 className={s.number}>4</h2>
                    <h1 className={s.item_title}>Инструменты анализа игры</h1>
                    <p className={s.item_sub_title}>Предоставляем доступ к программным решениям для автоматического
                        анализа</p>
                </div>
                <div className={s.item}>
                    <h2 className={s.number}>5</h2>
                    <h1 className={s.item_title}>Релокация</h1>
                    <p className={s.item_sub_title}>Хотите работать из другой точки мира? Мы поможем с решением вопросов
                        релокации</p>
                </div>
                <div className={s.last} onClick={() => setOpen(true)}>
                    <h1 className={s.last_item_title}>Подробнее о мерах поддержки</h1>
                    <div className={s.arrow}>
                        <Arrow/>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default Best;
