import React from 'react';
import s from './styles.module.css'
import {Accordion} from "../../../component/accordion";

const acc = [
    {
        title: 'Почему курс бесплатный?',
        desc: 'Одна из миссий нашей команды - менять жизни людей. Поэтому мы хотим дать возможность любому желающему попробовать свои силы и получить необходимые знания для того, чтобы по окончании курса уже иметь знания для прибыльной игры микролимитов. Кроме этого, все желающие могут попробовать что-то новое и понять, готовы ли они связывать свою жизнь с покером. Ведь как и в любом другом занятии, нее всегда наши ожидания совпадают с реальностью. Поэтому, перед тем, как связать себя контрактными обязательствами, мы даем возможность оценить свои силы и по окончании курса принять осознанное решение. Возможность сделать курс бесплатным обусловлена тем, что лучшие из прошедших курс получат предложение контракта от Команды и начнут зарабатывать профит. Наконец, мы хотим, чтобы по итогам прохождения курса в Команду попадали уже подготовленные игроки, поэтому наша задача - подготовить качественный курс, знаний которого уже достаточно для игры микролимитов в плюс.'
    },
    {
        title: 'Есть ли ко мне, как к участнику курса, какие-то требования?',
        desc: 'В целом, никаких специальных требований нет, поэтому курс могут пройти абсолютно все желающие, у кого есть интерес к покеру. Тем не менее, к курсантам у нас есть пожелания. Кроме общих пожеланий, таких как прилежность, активность и мотивированность, нам бы еще хотелось получать регулярную обратную связь по курсу. Поскольку мы хотим, чтобы наш курс был максимально эффективным и качественным, мы хотим знать, как можно его сделать лучше. Чем больше будет обратной связи, тем качественнее мы сможем сделать сам курс.'
    },
    {
        title: 'Могу совмещать курс с работой или учебой?',
        desc: 'Да, курс можно совмещать с работой, учебой или любым другим фуллтайм занятием, поскольку курс ориентировочно рассчитан на график: не более 2 часа 5 дней в неделю. График оценочный, поэтому вы, конечно, можете проходить курс в комфортном для вас темпе, ускоряясь или замедляясь по ходу в зависимости от ваших личных обстоятельств.'
    },
    {
        title: 'Покер - азартная игра. Не стану ли я зависимым лудоманом?',
        desc: 'Азартной игрой признается игра с целью выигрыша денег, в которой выигрыш в некоторой степени зависит от случая, а не от знаний или навыков играющих. И, поскольку, в покере присутствует элемент случайности, он вполне подходит под это определение и действительно является азартной игрой. Однако, следует отметить, что для профессиональных покерных игроков покер прежде всего связан с математикой и теорией вероятностей. Для того, чтобы быть плюсовым игроком в покер, необходимо совершать действия, приносящие на длинной дистанции положительное ожидание. Собственно, все обучение покеру как раз и направлено на то, чтобы обучить игрока покерной математике и вероятностям, а также научить использовать эти знания для получения прибыли. Один известный игрок сказал, что “один день в покере состоит из 90% удачи и 10% умения, а один год в покере состоит из 10% удачи и 90% умения”. Тем не менее, мы очень внимательно подходим к ответственной игре, и у нас выработаны определенные методы минимизации возможности развития игровой зависимости.'
    },
    {
        title: 'Сколько времени нужно выделять на обучение?',
        desc: 'Лучшие из прошедших курс получат предложение контракта от Команды, поэтому мы хотим, чтобы по итогам прохождения курса в Команду попадали уже подготовленные игроки. Поэтому мы рекомендуем уделять курсу как можно больше времени и внимания. Тем не менее, курс ориентировочно рассчитан на график: не более 2 часа 5 дней в неделю. График оценочный, поэтому вы, конечно, можете проходить курс в комфортном для вас темпе, ускоряясь или замедляясь по ходу в зависимости от ваших личных обстоятельств.'
    },
    {
        title: 'У меня есть хороший опыт игры в покер, есть ли курс для меня?',
        desc: <>Мы открыты для сотрудничества с игроками любого уровня, тем не менее, нам есть что предложить и опытным
            игрокам: как бекинг, так и бекинг с обучением от Команды. Если у вас уже есть значительный опыт и какие-либо
            достижения в покере, вы можете заполнить заявку на сайте <a
                href="https://firestorm.team" target={'_blank'}>https://firestorm.team</a>, с вами свяжутся
            представители Команды, где вы сможете обсудить ваш опыт и получить персональные предложения от Команды.</>
    }
]

const Fqa = () => {
    return (
        <div className={s.main}>
            <h1 className={s.title}>Часто задаваемые вопросы</h1>

            <div className={s.content}>
                {acc.map((el, i) => <Accordion key={i} label={el.title}>
                    {el.desc}
                </Accordion>)}
            </div>

            <div className={s.footer}>
                <p className={s.footer_title}>Не нашли ответа на свой вопрос?</p>
                <div className={s.btn}>
                    Напишите нам
                </div>
            </div>
        </div>
    );
};

export default Fqa;
