import React from 'react';
import s from './styles.module.css'
// import img from '../../../assets/img/learn.jpg'
import img from '../../../assets/img/learn.png'
import Button from "../../../component/button";
import AnchorLink from "react-anchor-link-smooth-scroll/lib/anchor-link";

const StartLearn = () => {
    return (
        <div className={s.box}>
            <div className={s.left_box}>
                <div className={s.cheaps}>
                    <div className={s.cheap}>Бесплатно</div>
                    <div className={s.cheap}>Для новичков</div>
                    <div className={s.cheap}>учись когда удобно</div>
                </div>
                <h1 className={s.title}>
                    погружение в покер:
                    <br/>
                    с нуля до уверенных побед
                </h1>

                <p className={s.sub_title}>Научим играть в покер с умом и выигрывать, а лучшим ученикам предоставим
                    инвестиции и пригласим в нашу команду.</p>
                <AnchorLink
                    offset={30}
                    href={`#tg_block`}
                >
                    <Button className={s.btn}>
                        Начать учиться
                    </Button>
                </AnchorLink>
            </div>

            <img className={s.img} src={img} alt="cards"/>
        </div>
    );
};

export default StartLearn;
