import React from 'react';
import s from './styles.module.css'
import icons from "../../../assets/svg/icon_s.png";
import iconm from "../../../assets/svg/icon_m.png";
import iconb from "../../../assets/svg/icon_b.png";
import {useInView} from "react-intersection-observer";

const Analityk = () => {
    const {ref, inView, entry} = useInView({
        threshold: 0.3,
    });

    return (
        <div className={s.main} style={{
            background:inView ? "#050321" : "#1E1D2B"
        }}>
            <div className={s.iconb}>
                <img src={iconb} alt=""/>
            </div>
            <div className={s.iconm}>
                <img src={iconm} alt=""/>
            </div>
            <div className={s.icons}>
                <img src={icons} alt=""/>
            </div>

            <p ref={ref} className={s.title}>этот курс мы разработали на основе аналитики игр нашей команды за 10 лет
                существования и упаковали в
                удобный для изучения формат</p>
        </div>
    );
};

export default Analityk;
