import React from 'react';
import {Box, Modal, SwipeableDrawer, useMediaQuery} from "@mui/material";
import s from './styles.module.css'
import {ReactComponent as CloseIcon} from "../../../assets/svg/close.svg";

const style = {
    position: 'absolute',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    maxWidth: '1000px',
    width: '100%',
    background: '#2B2A37',
    borderRadius: '32px',
    boxShadow: 0,
    display: 'flex',
    flexDirection: 'column',
    padding: '48px',
    height: '582px',
    '@media (max-width: 780px)': {
        width: '100%',
        height: '100dvh',
        borderRadius: 0,
        padding: '32px 16px 16px 16px'
    }
};

const style2 = {
    // position: 'absolute',
    // bottom: '0px',
    width: '100%',
    background: '#2B2A37',
    boxShadow: 0,
    display: 'flex',
    flexDirection: 'column',
    borderRadius: '16px 16px 0px 0px',
    height: '95dvh',
    padding: '0 16px',
    alignItems: 'center',
    justifyContent: 'center',
    color: '#FFF',
    fontFamily: 'Inter,sans-serif',
    fontSize: '16px',
    fontStyle: ' normal',
    fontWeight: ' 400',
    lineHeight: '140%',
    paddingTop: '12px',
    paddingBottom: '10px'
};

const data = [
    {
        number: 1,
        title: 'Бекинг',
        subtitle: <p className={s.modal_title}>Бекинг — это полное или частичное финансирование игрока для игры в
            различные дисциплины в покере.
            Инвестирование денег — не благотворительность, его цель — получить прибыль. Чаще всего в качестве спонсора
            на игру выступает организация, которая занимается финансированием и обучением игроков по контракту.Инвестор
            дает игроку деньги на игру и параллельно обучает. Профит игрока делится в заранее оговоренных долях до тех
            пор, пока не закончится контракт</p>
    },
    {
        number: 2,
        title: 'Обучение после курса',
        subtitle: <p className={s.modal_title}>Команда предоставляет бесплатное обучение после курса лучшим игрокам,
            прошедшим бесплатный курс
            Junior. Обучение - регулярное и систематизированное: минимум 4 тренировки в неделю, из которых 2 тренировки
            теоретические, а 2 - практические. Кроме этого, осуществляется постоянный контроль результатов игрока на
            основе индивидуально составленного персонального плана развития игрока - это специальная система оценки
            игры, в котором для игрока определяется цели, к которым он будет стремится в процессе обучения и развития.
            Также может быть определена точка, к которой игрок хочет дойти к сроку окончания контракта (например,
            закрепиться на каком-то лимите, выйти на определенную сумму ЗП и т.д.), а также прописаны действия, которые
            необходимо предпринять, чтобы этой цели добиться.</p>
    },
    {
        number: 3,
        title: 'Поддержка игроков',
        subtitle: <p className={s.modal_title}>Команда предоставляет круглосуточную поддержку игрокам, которая помогает
            решить широчайший круг
            вопросов и проблем, связанных с игрой (и не только!): от установки и настройки программ для игры, помощи в
            выборе лучших румов и времени для игры и вплоть до консультаций и советов по игровым и околоигровым
            вопросам, общении со службой поддержки румов, а также психологической поддержки игроков и помощи в релокации
            для давних и проверенных игроков.</p>
    },
    {
        number: 4,
        title: 'Инструменты анализа игры',
        subtitle: <>
            <p className={s.modal_title}>Команда предоставляет для неограниченного использования сервер со
                специализированным софтом для анализа
                игры и развития:</p>
            <div className={s.ul}>
                <p>HoldemResources Calculator (HRC)</p>
                <p>CardRunners EV (CREV)</p>
                <p>Pokerstrategy Equilab</p>
                <p>FlopZilla</p>
                <p>Icmizer</p>
                <p>Gto+</p>
                <p>HoldEq</p>
                <p>SimplePreflopHoldem</p>
                <p>SimplePostflopHoldem</p>
                <p>Simple 3 way</p>
                <p>GTO Trainer</p>
                <p>GTO Base</p>
                <p>GTODB</p>
            </div>
            <p className={s.modal_title}>Но и это еще не всё! Кроме списка популярного софта, команда имеет собственные
                разработки. Например,
                совместно с нашими партнерами - PokerHub - был разработан собственный тренажер для тренировки и изучения
                префлоп-чартов, а также специальный ликбастер, позволяющий загрузить базу и быстро найти лики в игре,
                чтобы понимать, над чем работать в первую очередь.</p>
        </>
    },
    {
        number: 5,
        title: 'Релокация',
        subtitle: <p className={s.modal_title}>Для игроков, которые давно находятся в команде и зарекомендовали себя
            надежными партнерами,
            оказывается помощь в релокации из стран текущего проживания в более безопасные места, более безопасные, либо
            туда, где не будет ничего отвлекать от игры и саморазвития. Команда периодически готовит для своих игроков
            конкретные предложения по релокации, с поддержкой от Команды. Это могут быть как разовые проекты, так и
            сезонные (например, зимовки покеристов в теплых краях Индонезии, Таиланда, Вьетнама и т. д.)</p>
    },
]
const ModalSupport = ({handleClose, open, setOpen}) => {
    const matches = useMediaQuery('(max-width:700px)');

    const container = window !== undefined ? () => window.document.body : undefined;
    const toggleDrawer = (newOpen) => () => {
        setOpen(newOpen);
    };
    const iOS =
        typeof navigator !== 'undefined' && /iPad|iPhone|iPod/.test(navigator.userAgent);
    return matches ? <SwipeableDrawer
        disableBackdropTransition={!iOS} disableDiscovery={iOS}
        sx={{

            '& .MuiPaper-root': {
                background: 'rgba(0, 0, 0, 0.5);',
                borderRadius: '16px 16px 0px 0px',
                // opacity:'0'
            }
        }}
        container={container}
        anchor="bottom"
        open={open}
        onClose={toggleDrawer(false)}
        onOpen={toggleDrawer(true)}
        swipeAreaWidth={36}
        disableSwipeToOpen={true}
        ModalProps={{
            keepMounted: true,
        }}
    >

        <div className={s.line}/>

        <Box sx={style2}>
            <div className={s.header}>
                <div className={s.close} onClick={handleClose}>
                    <CloseIcon/>
                </div>
                <h1 className={s.title}>Меры поддержки для игроков</h1>
                <p className={s.sub_title}>Получивших спонсорские контракты</p>
            </div>

            <div className={s.content}>
                <h1 className={s.title}>Меры поддержки для игроков</h1>
                <p className={s.sub_title}>Получивших спонсорские контракты</p>
                {data.map((el) => <div className={s.item}>
                    <div className={s.item_top}>
                        <div className={s.number}>{el.number}</div>
                        <p className={s.item_title}>{el.title}</p>
                    </div>
                    <div className={s.item_bottom}>
                        {el.subtitle}
                    </div>
                </div>)}
            </div>
        </Box>
    </SwipeableDrawer> : <Modal
        open={open}
        onClose={handleClose}
    >
        <Box sx={style}>
            <div className={s.header}>
                <div className={s.close} onClick={handleClose}>
                    <CloseIcon/>
                </div>
                <h1 className={s.title}>Меры поддержки для игроков</h1>
                <p className={s.sub_title}>Получивших спонсорские контракты</p>
            </div>

            <div className={s.content}>
                {data.map((el) => <div className={s.item}>
                    <div className={s.item_top}>
                        <div className={s.number}>{el.number}</div>
                        <p className={s.item_title}>{el.title}</p>
                    </div>
                    <div className={s.item_bottom}>
                        {el.subtitle}
                    </div>
                </div>)}
            </div>
        </Box>
    </Modal>

};

export default ModalSupport;
