import React, {useState} from 'react';
import s from './styles.module.css'
import Item from "./item";
import classNames from "classnames";

const data_1 = [
    {title: 'Откуда в покере деньги', number: '1.1'},
    {title: 'Правила покера', number: '1.2'},
    {title: 'Знакомство с дисциплиной Spin&Go и ее особенностями', number: '1.3'},
]

const data_2 = [
    {title: 'Введение в префлоп', number: '2.1'},
    {title: 'Префлоп стратегия на позиции Button (BU) в 3-max', number: '2.2'},
    {title: 'Префлоп-стратегия на позиции Small Blind (SB) в 3-max. Вводная часть', number: '2.3'},
    {title: 'Префлоп стратегия на позиции SB в 3-max в неоткрытом банке (SBB)', number: '2.4'},
    {title: 'Префлоп стратегия на позиции SB  в 3-max в открытом банке против лимпа Button', number: '2.5'},
]
const data_3 = [
    {title: 'Вводный урок по постфлопу', number: '3.1'},
    {title: 'Атака (инициатива) в позиции', number: '3.2'},
    {title: 'Атака (инициатива) без позиции', number: '3.3'},
    {title: 'Защита (без инициативы) в позиции', number: '3.4'},
    {title: 'Защита (без инициативы) без позиции', number: '3.5'},
]
const Progrm = () => {
    const [showMore, setShowMore] = useState(false)

    const data_2_check = showMore ? [...data_2, ...data_2] : data_2
    return (
        <div className={s.main}>
            <div className={s.header}>
                <h1 className={s.header_title}>Программа курса</h1>

                <div className={s.header_description}>
                    <div className={s.header_item}>
                        <div className={s.header_item_circle}>2</div>
                        <p className={s.header_item_title}>часа в день</p>
                    </div>
                    <div className={s.header_item}>
                        <div className={s.header_item_circle}>6</div>
                        <p className={s.header_item_title}>недель</p>
                    </div>
                    <div className={s.header_item}>
                        <div className={s.header_item_circle}>25+</div>
                        <p className={s.header_item_title}>уроков</p>
                    </div>
                    <div className={s.header_item}>
                        <div className={s.header_item_circle}>60+</div>
                        <p className={s.header_item_title}>практических заданий</p>
                    </div>
                </div>
            </div>

            <div className={s.content}>
                <div className={s.box}>
                    <h1 className={s.box_title}>Раздел 1. Введение в покер</h1>
                    <p className={classNames(s.box_sub_title, s.box_sub_title1)}>Узнаешь о том, за счет чего игра в
                        покер приносит доход, а также
                        познакомишься с правилами техасского холдема и особенностями дисциплины Spin’n’Go.</p>

                    <div className={s.items}>
                        {data_1.map((el) => <Item {...el} key={el.number}/>)}
                    </div>
                </div>
                <div className={s.box}>
                    <h1 className={s.box_title}>Раздел 2. Оптимальная стратегия на префлопе</h1>
                    <p className={classNames(s.box_sub_title, s.box_sub_title2)}>В этом блоке содержится исчерпывающая
                        информация об оптимальной стратегии на префлопе против слабых оппонентов. Большинство уроков
                        предполагает наличие теоретического материала, небольшого тестового задания и тренировки в
                        командном префлоп тренажере.</p>

                    <div className={s.items}>
                        {data_2_check.map((el, i) => <Item {...el} key={el.number + i}/>)}
                    </div>

                    <div className={s.show_more}
                         onClick={() => setShowMore(!showMore)}>{showMore ? 'Скрыть' : 'Показать все'}</div>
                </div>
                <div className={s.box}>
                    <h1 className={s.box_title}>Раздел 3. Основы стратегии на постфлопе</h1>
                    <p className={classNames(s.box_sub_title, s.box_sub_title3)}>Расскажем о начальных (базовых)
                        принципах формирования правильной стратегии. Как в целом, так и по четырем основным типам
                        ситуаций.</p>

                    <div className={s.items}>
                        {data_3.map((el) => <Item {...el} key={el.number}/>)}
                    </div>
                </div>
            </div>
        </div>
    );
};

export default Progrm;
